// app/(seguridad)/page.tsx
"use client";

import { useState, useEffect, useRef } from "react";
import Image from "next/image";
import { motion } from "framer-motion";
import {
  Eye, Shield, Clock, AlertTriangle, Radio, Coffee,
} from "lucide-react";

import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";

import {
  Sospechoso,
  sospechosos,
  frasesSimpson,
} from "./data/sospechosos";

/* -------------------------------------------------------------------------- */
/*                             Config visual r√°pida                           */
/* -------------------------------------------------------------------------- */

const nivelColors: Record<Sospechoso["nivelPeligro"], string> = {
  BAJO: "bg-green-500",
  MEDIO: "bg-yellow-500",
  ALTO: "bg-orange-500",
  EXTREMO: "bg-red-500",
};

/* -------------------------------------------------------------------------- */
/*                                 Componente                                 */
/* -------------------------------------------------------------------------- */
/**
 * **SeguridadKwikEMart**
 *
 * Panel (casi ‚Äúmini-juego‚Äù) de vigilancia:
 * - Muestra una lista de sospechosos con su nivel de peligro.
 * - Al pulsar ‚Äú¬°AVISTADO!‚Äù se:
 *   1. Cambia `ultimaVezVisto`.
 *   2. Activa alarma visual + sonido + GIF (Apu shotgun).
 *   3. Deshabilita botones 10 s para evitar spam.
 */
export default function SeguridadKwikEMart() {
  /* --------------------------- Estado principal --------------------------- */
  const [revoltosos, setRevoltosos]   = useState<Sospechoso[]>(sospechosos);
  const [alertaActiva, setAlertaActiva] = useState(false);
  const [ultimoReporte, setUltimoReporte] = useState("");
  const [horaActual, setHoraActual]   = useState(new Date());
  const [fraseAleatoria, setFraseAleatoria] = useState("");
  const [showShotgun, setShowShotgun] = useState(false);
  const [botonesDeshabilitados, setBotonesDeshabilitados] = useState(false);

  /* Refs para audio */
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const alarmRef = useRef<HTMLAudioElement | null>(null);

  /* ------------------------------ Efectos --------------------------------- */

  /* Reloj cada minuto */
  useEffect(() => {
    const t = setInterval(() => setHoraActual(new Date()), 60_000);
    return () => clearInterval(t);
  }, []);

  /* Seleccionar frase initial */
  useEffect(() => {
    setFraseAleatoria(frasesSimpson[Math.floor(Math.random() * frasesSimpson.length)]);
  }, []);

  /* Manejar sonido de alarma */
  useEffect(() => {
    if (alertaActiva) {
      const alarm = new Audio("/mp3/alarma.mp3");
      alarm.loop = true;
      alarm.volume = 0.5;
      alarmRef.current = alarm;
      alarm.play().catch(() => console.warn("No se pudo reproducir alarma"));
    } else {
      alarmRef.current?.pause();
      if (alarmRef.current) alarmRef.current.currentTime = 0;
    }
  }, [alertaActiva]);

  /* Cerrar GIF + reset */
  useEffect(() => {
    if (!showShotgun) return;
    const to = setTimeout(() => {
      setShowShotgun(false);
      setAlertaActiva(false);
      audioRef.current?.pause();
      if (audioRef.current) audioRef.current.currentTime = 0;
    }, 8_600);
    return () => clearTimeout(to);
  }, [showShotgun]);

  /* ----------------------------- Handlers --------------------------------- */

  /** Marca avistamiento y dispara efectos. */
  const handleAvistarSospechoso = (id: number, nombre: string) => {
    const hora = new Date();
    const horaFmt = hora.toLocaleTimeString("es-CO", { hour: "2-digit", minute: "2-digit" });

    // Actualizar √∫ltima vez visto
    setRevoltosos((prev) =>
      prev.map((r) => (r.id === id ? { ...r, ultimaVezVisto: `AHORA (${horaFmt})` } : r)),
    );
    setUltimoReporte(`${nombre} avistado a las ${horaFmt}`);
    setAlertaActiva(true);

    // Bloqueo de botones 10 s
    setBotonesDeshabilitados(true);
    setTimeout(() => setBotonesDeshabilitados(false), 10_000);

    // Disparo escopeta (retardo para sincronizar con GIF)
    const escopeta = new Audio("/mp3/escopetazo.mp3");
    escopeta.volume = 1;
    audioRef.current = escopeta;
    setTimeout(() => escopeta.play().catch(() => {}), 4_850);

    setShowShotgun(true);
  };

  const fmtHora = (d: Date) =>
    d.toLocaleTimeString("es-CO", { hour: "2-digit", minute: "2-digit", second: "2-digit" });

  /* ------------------------------ Render ---------------------------------- */
  return (
    <div className="relative min-h-screen bg-gradient-to-b from-yellow-300 via-yellow-400 to-yellow-200 dark:from-gray-900 dark:via-gray-800 dark:to-gray-700">

      {/* Overlay rojo pulsante cuando hay alerta */}
      {alertaActiva && (
        <div className="pointer-events-none fixed inset-0 z-50 bg-gradient-to-b from-red-500/80 via-red-600/30 to-red-600/80 animate-pulse" />
      )}

      {/* GIF de Apu con shotgun (framer-motion para desplazamiento) */}
      {showShotgun && (
        <motion.div
          className="fixed z-60"
          style={{ top: "50%", left: "50%" }}
          initial={{ x: "-150%", y: "-50%" }}
          animate={{ x: "-50%",  y: "-50%" }}
          transition={{ duration: 3 }}
        >
          <Image src="/images/gif.gif" alt="Apu con escopeta" width={700} height={700} unoptimized />
        </motion.div>
      )}

      {/* ----------------------- Encabezado ----------------------- */}
      <header className="bg-blue-700 dark:bg-slate-800 shadow-xl border-b-4 border-yellow-400 p-8">
        <div className="flex flex-col md:flex-row items-center justify-between gap-4">
          <div className="text-center md:text-left">
            <h1 className="text-4xl md:text-6xl font-bold text-yellow-300 drop-shadow-[3px_3px_0px_#000]">
              KWIK-E-MART üö®
            </h1>
            <h2 className="text-xl font-semibold text-white mt-2">Centro de Seguridad y Vigilancia</h2>
            <p className="text-blue-100 italic mt-1">‚Äî {fraseAleatoria} ‚Äî</p>
          </div>

          <div className="bg-black/30 rounded-lg p-4 text-center">
            <div className="flex items-center gap-2 text-white mb-2">
              <Clock className="w-5 h-5" />
              <span className="font-mono text-lg">{fmtHora(horaActual)}</span>
            </div>
            <div className="flex items-center gap-2 text-yellow-300">
              <Shield className="w-4 h-4" />
              <span className="text-sm">Sistema Activo</span>
            </div>
          </div>
        </div>
      </header>

      {/* ----------------------- Panel Gorgory -------------------- */}
      <div className="max-w-6xl mx-auto px-6 py-6">
        <Card className="mb-6 border-4 border-blue-600 bg-gradient-to-r from-blue-50 to-indigo-100 dark:from-blue-950 dark:to-indigo-950">
          <CardHeader className="pb-1">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-blue-600 rounded-full flex items-center justify-center">
                <Radio className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-bold text-blue-800 dark:text-blue-200">üìª Central del Jefe Gorgory</h3>
                <p className="text-blue-600 dark:text-blue-300 text-sm">Comunicaci√≥n directa con la polic√≠a</p>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            {alertaActiva && ultimoReporte ? (
              <Alert className="border-red-500 bg-red-50 dark:bg-red-950/50">
                <AlertTriangle className="h-4 w-4 text-red-600" />
                <AlertDescription className="text-red-800 dark:text-red-200 font-semibold">
                  üö® ALERTA: {ultimoReporte} ‚Äì Notificando al Jefe Gorgory‚Ä¶
                </AlertDescription>
              </Alert>
            ) : (
              <div className="flex items-center gap-2 text-gray-600 dark:text-gray-400">
                <Coffee className="w-4 h-4" />
                <span>El Jefe Gorgory est√° tomando caf√© y comiendo donas üç©</span>
              </div>
            )}
          </CardContent>
        </Card>

        {/* ----------------------- M√©tricas r√°pidas ------------------------ */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
          {[
            {
              color: "green",
              valor: revoltosos.length,
              label: "Sospechosos Activos",
            },
            {
              color: "yellow",
              valor: revoltosos.filter((r) => r.ultimaVezVisto.includes("AHORA")).length,
              label: "Avistamientos Hoy",
            },
            {
              color: "red",
              valor: revoltosos.filter((r) => ["ALTO", "EXTREMO"].includes(r.nivelPeligro)).length,
              label: "Alta Prioridad",
            },
            {
              color: "purple",
              valor: "$ " + revoltosos.reduce((sum, r) => sum + (r.recompensa ?? 0), 0),
              label: "Recompensa Total",
            },
          ].map(({ color, valor, label }) => (
            <Card key={label} className={`bg-${color}-100 dark:bg-${color}-900/30 border-${color}-500`}>
              <CardContent className="p-4 text-center">
                <div className={`text-2xl font-bold text-${color}-700 dark:text-${color}-300`}>{valor}</div>
                <div className={`text-sm text-${color}-600 dark:text-${color}-400`}>{label}</div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* ----------------------- Lista de sospechosos -------------------- */}
        <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
          {revoltosos.map((s) => (
            <Card
              key={s.id}
              className="border-4 border-yellow-600 bg-white dark:bg-gray-800 shadow-xl hover:border-red-500 hover:scale-105 transition duration-300"
            >
              <CardHeader className="pb-2">
                <div className="flex items-center justify-between">
                  <Badge className={`${nivelColors[s.nivelPeligro]} text-white font-bold px-3 py-1`}>
                    {s.nivelPeligro}
                  </Badge>
                  {s.recompensa && (
                    <Badge variant="outline" className="bg-green-100 text-green-800 border-green-500">
                      üí∞ ${s.recompensa}
                    </Badge>
                  )}
                </div>
              </CardHeader>

              <CardContent className="text-center space-y-4">
                <div className="relative w-32 h-32 mx-auto">
                  <div className="absolute inset-0 bg-gradient-to-br from-yellow-400 to-orange-500 rounded-full animate-pulse" />
                  <div className="relative w-full h-full bg-gray-200 dark:bg-gray-700 rounded-full border-4 border-red-500 overflow-hidden">
                    <Image src={s.imagen} alt={s.nombre} fill className="object-cover" />
                  </div>
                </div>

                <h3 className="text-2xl font-bold text-gray-800 dark:text-white">{s.nombre}</h3>

                <div className="bg-gray-100 dark:bg-gray-700 rounded-lg p-3">
                  <h4 className="font-semibold text-gray-700 dark:text-gray-300 mb-1">üìã Descripci√≥n:</h4>
                  <p className="text-sm text-gray-600 dark:text-gray-400">{s.descripcion}</p>
                </div>

                <div className="flex items-center justify-center gap-2 text-sm">
                  <Eye className="w-4 h-4 text-blue-600" />
                  <span className="text-gray-600 dark:text-gray-400">√öltimo avistamiento:</span>
                  <strong className="text-red-600 dark:text-red-400">{s.ultimaVezVisto}</strong>
                </div>

                <Button
                  onClick={() => handleAvistarSospechoso(s.id, s.nombre)}
                  disabled={botonesDeshabilitados}
                  className={`w-full font-bold py-3 rounded-lg ${botonesDeshabilitados
                    ? "bg-gray-400 cursor-not-allowed"
                    : "bg-gradient-to-r from-red-500 to-red-600 hover:from-red-600 hover:to-red-700"}`}
                >
                  <Radio className="w-4 h-4 mr-2" />
                  {botonesDeshabilitados ? "Espera‚Ä¶" : "üö® ¬°AVISTADO! Alertar Jefe Gorgory"}
                </Button>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* ---------------------------- Footer ----------------------------- */}
        <footer className="mt-12 text-center text-gray-600 dark:text-gray-400 text-sm">
          ¬°Gracias por mantener Springfield a salvo! ‚Äì Apu Nahasapeemapetilon
          <p className="text-xs text-gray-500 dark:text-gray-400 mt-2">¬© 2025 Kwik-E-Mart. Todos los derechos reservados.</p>
        </footer>
      </div>
    </div>
  );
}
